/**
 * SwipeAction 滑动操作
 * @description 通过滑动触发选项的容器
 * @tutorial https://ext.dcloud.net.cn/plugin?id=181
 */
export default {
	data() {
		return {};
	},
	provide() {
		return {
			swipeaction: this
		}
	},
	created() {
		this.children = []
	},
	methods: {
		closeOther(vm) {
			let children = this.children
			children.forEach((item, index) => {
				if (vm === item) return
				// 支付宝执行以下操作






				// app vue 端、h5 、微信、支付宝  执行以下操作

				let position = item.position[0]
				let show = position.show
				if (show) {
					position.show = false
				}


				// nvue 执行以下操作



			})
		}
	}
}
